import wx from 'weixin-js-sdk'
import { MessageBox } from 'mint-ui';
import { WxConfig } from 'api/wxapi'
import { ERR_CODE } from 'api/config'
import { AddressList } from 'api/address'
import { UserInfo } from 'common/js/common'
import { ShowToast,Loading } from 'common/js/loading-toast'
export default {
	data() {
		return {
			remark: '',
			goods: {},
			addressId: '',
			userInfo: '',
			addressList: [],
		}
	},
	methods: {
		wxconfigInit() {
			if(this.addressId === '') {
				MessageBox.confirm('请添加默认地址').then(action => {
					this.$router.push({
						path: '/my-address'
					})
				}).catch(res => {

				});
				return;
			}
			let options = {
				openid: this.userInfo.openId,
				remark: this.remark,
				addressId: this.addressId,
				total_fee: this.goods.count.toFixed(2),
				goodsList: this.goods.goodsList
			}
			let that = this;
			WxConfig(options).then(res => {
				let data = res.data;
				let options = res.data;
				options = JSON.stringify(options);
				localStorage.setItem('wxconfig', options)
				wx.config({
					debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
					appId: data.appid, // 必填，企业号的唯一标识，此处填写企业号corpid
					timestamp: data.timeStamp, // 必填，生成签名的时间戳
					nonceStr: data.nonce_str, // 必填，生成签名的随机串
					signature: data.paySign, // 必填，签名，见附录1
					jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
				});
				wx.ready(function() {
					that.addCart(data);
					// config信息验证后会执行ready方法，所有接口调用都必须在config接口获得结果之后，config是一个客户端的异步操作，所以如果需要在页面加载时就调用相关接口，则须把相关接口放在ready函数中调用来确保正确执行。对于用户触发时才调用的接口，则可以直接调用，不需要放在ready函数中。
				});
				wx.error(function(res) {
					console.log(res)
					// config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
				});
			})
		},
		getAddressList() {
			AddressList(this.userInfo.openId).then(res => {
				if(res.data.code === ERR_CODE) {
					this.addressList = res.data.data;
					for(let i = 0; i < res.data.data.length; i++) {
						if(res.data.data[i].type === 1) {
							this.addressId = res.data.data[i].id;
						}
					}
				}
			}).catch(res => {})
		},
		addCart(data) {
			Loading();
			//				let wxconfig = window.localStorage.getItem('wxconfig');
			//				wxconfig = wxconfig ? JSON.parse(wxconfig) : '';
			wx.chooseWXPay({
				timestamp: data.timeStamp, // 支付签名时间戳
				nonceStr: data.nonce_str, // 支付签名随机串，不长于32 位
				package: data.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
				signType: "MD5", // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
				paySign: data.paySign, // 支付签名
				success: function(res) {
					console.log(res)
					Loading(true);
					//支付成功
					ShowToast('支付成功');

				},
				cancel: function(res) {
					Loading(true);
					ShowToast('取消支付');
					

					//支付取消
				},
				fail: function(res) {
					Loading(true);
					ShowToast('支付失败');
				}
			});
		},
		goAddress() {
			this.$router.push({
				path: '/my-address'
			})
		}
	},
	created() {
		this.goods = this.$route.params;
		
	},
	mounted() {
		this.userInfo = UserInfo();
		this.getAddressList();
		
	},
	watch: {
		$route(val) {
				console.log(val)
			
			if(val.path === '/placeOrder') {
				this.getAddressList();

			}
		}
	}

	//		beforeRouteLeave(to, from, next) {
	//			this.goods = {};
	//			localStorage.removeItem('payInfo');
	//	        next();
	//		},
}



